import { AUTH_ROUTES_KEY } from './auth.const'

export enum AuthErrorCodes {
  invalid = 'INVALID_CREDENTIALS',
  conflict = 'SESSION_CONFLICT',
  authUnexpected = 'UNEXPECTED_AUTHORIZATION_HEADER',
  authUnsupported = 'UNSUPPORTED_AUTHORIZATION_HEADER',
  notAuthenticated = 'NOT_AUTHENTICATED',
  notAuthorized = 'NOT_AUTHORIZED',
  userSamePassword = 'USER_SAME_PASSWORD',
  userWrongPassword = 'USER_WRONG_PASSWORD',
}

export enum AuthRoutes {
  authenticate = `${AUTH_ROUTES_KEY}/authenticate`,
  changePassword = `${AUTH_ROUTES_KEY}/change-password`,
  logout = `${AUTH_ROUTES_KEY}/logout`,
  resetPassword = `${AUTH_ROUTES_KEY}/reset-password`,
  signIn = `${AUTH_ROUTES_KEY}/sign-in`,
}
